<script>
export default {
  methods: {
    onClickMessageBox () {
      this.$messageBox({
        cancelText: '取消3',
        confirmText: '确定4',
        messageContent: {
          title: '标题5',
          content: '表哦提666'
        },
        succ:() => {
          this.$logger.log(1);
        },
        fail:()=>{
          this.$logger.log(0);
        }
      })
    }
  }
}
</script>


